/* EventDetails.css */

.event-details-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent overlay */
    display: flex;
    justify-content: center;
    align-items: flex-end; /* Start from the bottom */
    transition: transform 0.5s ease-in-out;
    transform: translateY(100%); /* Initially off-screen, starting from the bottom */
  }
  
  .event-details {
    width: 100%;
    max-width: 600px; /* Adjust the maximum width as needed */
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.5s ease-in-out;
  }
  
  /* Apply this class when the modal is visible */
  .event-details.slide-up {
    transform: translateY(0); /* Slide up to the center */
  }
  
  .close-button {
    margin-top: 10px;
    padding: 8px 16px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .close-button:hover {
    background-color: #0056b3;
  }
  
  